.button {
  border: none;
  width: 50px;
  height: 30px;
  background-color: #f1f1f1;
}

.button:hover {
  background-color: #ddd;
}

.button:focus {
  background-color: #bbb;
  outline: none;
}

.active {
  background-color: #bbb;
  outline: none;
}

.content {
  border: solid 1px #f1f1f1;
  width: 300px;
  height: 100px;
}
